  exports.default = Ember.HTMLBars.template({ "id": "XYykFEjo", "block": "{\"statements\":[[11,\"div\",[]],[15,\"class\",\"jumbo\"],[15,\"align\",\"center\"],[13],[0,\"\\n  \"],[11,\"p\",[]],[15,\"class\",\"heading\"],[15,\"align\",\"center\"],[13],[0,\"\\n    \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/KidsLogo.png\"]]],[13],[0,\"\\n      \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/KidsLogo.png\"]]],[15,\"width\",\"300\"],[15,\"height\",\"200\"],[13],[14],[0,\"\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n  \"],[11,\"div\",[]],[15,\"class\",\"layout-row layout-wrap\"],[13],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-20\"],[13],[0,\"\\n      \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/kids-church.jpg\"]]],[13],[0,\"\\n        \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/kids-church.jpg\"]]],[15,\"width\",\"300\"],[15,\"height\",\"200\"],[13],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-60\"],[13],[0,\"\\n      \"],[11,\"div\",[]],[13],[0,\"\\n        \"],[11,\"h2\",[]],[13],[0,\"KID'S CHURCH\"],[14],[0,\"\\n        \"],[11,\"p\",[]],[13],[0,\"\\n        Kids Church is for the younger children during the 10:30 am worship service. Children will meet in the youth room and then go to the worship service to participate in Small Talk given by the Pastor at the beginning of the worship service. The kids then go to the youth room to participate in activities and songs that praise the Lord. The doors to Kids Church open around 10:20 am on Sunday mornings. The youth room is located on the far west side of the church complex.\\n        \"],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n  \"],[11,\"div\",[]],[15,\"class\",\"layout-row layout-wrap\"],[13],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-60\"],[13],[0,\"\\n      \"],[11,\"div\",[]],[13],[0,\"\\n        \"],[11,\"p\",[]],[13],[11,\"h2\",[]],[13],[0,\"SUNDAY SCHOOL\"],[14],[14],[0,\"\\n        \"],[11,\"p\",[]],[13],[0,\"\\n        Pilgrims Sunday School program is a wonderful opportunity for your children, whether they are 2 years old or 18 years old, to experience the love of Christ through the studying of His Word.  Pilgrim is blessed with a great staff of Sunday School teachers that are passionate and creative in inspiring your children to draw closer to God. The Sunday School classrooms are divided up by grades and Pilgrim utilizes more than a dozen classrooms to accomodate all of the children that attend. Pilgrims Sunday School curriculum challenges children to understand that Jesus is their Lord and Savior through fun activities, songs and Bible stories.\\n        \"],[14],[0,\"\\n        \"],[11,\"br\",[]],[13],[14],[0,\"\\n        \"],[11,\"p\",[]],[13],[0,\"\\n        Sunday School classes start at \"],[11,\"b\",[]],[13],[0,\"9:15 on Sunday mornings\"],[14],[0,\" and run until approximately 10:15. Please feel free to drop in sometime! Each year Pilgrim also has a wonderful Childrens Christmas Program.\\n        \"],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-20\"],[13],[0,\"\\n\\n      \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/sundaySchool.jpg\"]]],[13],[0,\"\\n        \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/sundaySchool.jpg\"]]],[15,\"width\",\"300\"],[15,\"height\",\"200\"],[13],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n\"],[14],[0,\"\\n\"]],\"locals\":[],\"named\":[],\"yields\":[],\"hasPartials\":false}", "meta": { "moduleName": "pilgrim-frontend/templates/childrens.hbs" } });
