  exports.default = Ember.HTMLBars.template({ "id": "HRGFQQhK", "block": "{\"statements\":[[11,\"div\",[]],[15,\"class\",\"jumbo\"],[15,\"align\",\"center\"],[13],[0,\"\\n  \"],[11,\"p\",[]],[15,\"class\",\"heading\"],[15,\"align\",\"center\"],[13],[0,\"\\n    \"],[11,\"h1\",[]],[13],[0,\"Youth\"],[14],[0,\"\\n  \"],[14],[0,\"\\n\\n\\n  \"],[11,\"div\",[]],[15,\"class\",\"layout-row layout-wrap\"],[13],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-grow\"],[13],[0,\"\\n      \"],[11,\"p\",[]],[13],[0,\"\\n      Pilgrim is all about families. Its about relationships with each other and with Christ. Its also about providing love, guidance and support to our children and letting them know that Jesus Christ will be with them always. Pilgrim loves kids! In 1998, Pilgrim built a new 11 acre campus in Bellevue. The building design specifically has children in mind. Our Childcare Center has more than 150 children in attendance. The classrooms at Pilgrim are specifically designed for kids. The outdoor playground, playing fields and indoor gymnasium are also designed for kids.\\n      \"],[14],[0,\"\\n      \"],[11,\"p\",[]],[13],[0,\"\\n      We want your kids to join us as Pilgrim Kids! Take a look at the below at some of the exciting kids programs that are available at Pilgrim.\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n\\n  \"],[11,\"div\",[]],[15,\"class\",\"layout-row layout-wrap\"],[13],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-20\"],[13],[0,\"\\n      \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/SeniorYouth.jpg\"]]],[13],[0,\"\\n        \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/SeniorYouth.jpg\"]]],[15,\"width\",\"200\"],[15,\"height\",\"100\"],[13],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-60\"],[13],[0,\"\\n      \"],[11,\"div\",[]],[13],[0,\"\\n        \"],[11,\"p\",[]],[13],[11,\"h2\",[]],[13],[0,\"Senior Youth\"],[14],[14],[0,\"\\n        \"],[11,\"p\",[]],[13],[0,\"\\n        On Sunday mornings, we meet at 9:15 am for a lively discussion on topics that directly affect you. Come join us!  Check out the rest of the site for other activities and events scheduled over the next few months.  God loves you and so do we!\\n        \"],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n\\n\\n  \"],[11,\"div\",[]],[15,\"class\",\"layout-row layout-wrap\"],[13],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-60\"],[13],[0,\"\\n      \"],[11,\"div\",[]],[13],[0,\"\\n        \"],[11,\"p\",[]],[13],[11,\"h2\",[]],[13],[0,\"Junior Youth\"],[14],[14],[0,\"\\n        \"],[11,\"p\",[]],[13],[0,\"\\n        Junior Youth is a great way to meet new friends and to experience the love of Jesus Christ! We are an active group, getting together often to study His Word and to grow in our faith. We will add more information to this site soon. Check out our calendar for this month. We would love to see you in church and at some of these activities!\\n        \"],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-20\"],[13],[0,\"\\n\\n      \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/JuniorYouth.jpg\"]]],[13],[0,\"\\n        \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/JuniorYouth.jpg\"]]],[15,\"width\",\"200\"],[15,\"height\",\"100\"],[13],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n\\n  \"],[11,\"div\",[]],[15,\"class\",\"layout-row layout-wrap\"],[13],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-20\"],[13],[0,\"\\n      \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/Scouts.jpg\"]]],[13],[0,\"\\n        \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/Scouts.jpg\"]]],[15,\"width\",\"200\"],[15,\"height\",\"100\"],[13],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-60\"],[13],[0,\"\\n      \"],[11,\"div\",[]],[13],[0,\"\\n        \"],[11,\"p\",[]],[13],[11,\"h2\",[]],[13],[0,\"Scouts\"],[14],[14],[0,\"\\n        \"],[11,\"p\",[]],[13],[0,\"\\n        The Boy Scouts currently meet on Monday evenings at 7 pm.  The troop dues are $10.00 per month (camping fees are based on each campout separately  food, etc).\\n        \"],[14],[0,\"\\n        \"],[11,\"p\",[]],[13],[0,\"\\n        If you are new to the area and looking for a Scout troop, we would love to have you visit us.\\n        \"],[14],[0,\"\\n        \"],[11,\"p\",[]],[13],[0,\"\\n        Visit the \"],[11,\"a\",[]],[15,\"href\",\"http://www.troop476.net/\"],[15,\"target\",\"_blank\"],[13],[0,\" Boy Scout Troop #476\"],[14],[0,\" web sites for additional information.\\n        \"],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n\"],[14],[0,\"\\n\"]],\"locals\":[],\"named\":[],\"yields\":[],\"hasPartials\":false}", "meta": { "moduleName": "pilgrim-frontend/templates/youth.hbs" } });
