  exports.default = Ember.HTMLBars.template({ "id": "tzGJyjVT", "block": "{\"statements\":[[11,\"div\",[]],[15,\"class\",\"jumbo\"],[15,\"align\",\"center\"],[13],[0,\"\\n  \"],[11,\"p\",[]],[15,\"class\",\"heading\"],[15,\"align\",\"left\"],[13],[0,\"\\n  \"],[11,\"h1\",[]],[13],[0,\"About Our Church\"],[14],[0,\"\\n  \"],[14],[0,\"\\n\\n  \"],[11,\"div\",[]],[15,\"class\",\"layout-row layout-wrap\"],[15,\"align\",\"left\"],[13],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-40\"],[13],[0,\"\\n      \"],[11,\"h2\",[]],[13],[0,\"Our Vision\"],[14],[0,\"\\n      \"],[11,\"p\",[]],[13],[0,\"\\n        Where by Gods grace, believers from all walks of life together WORSHIP Christ, GROW spiritually in Christ and SHARE Christs love with others!\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-20\"],[13],[0,\"\\n    \"],[14],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"flex-40\"],[13],[0,\"\\n      \"],[11,\"h2\",[]],[13],[0,\"Our Mission\"],[14],[0,\"\\n      \"],[11,\"p\",[]],[13],[0,\"\\n        Our mission statement can be summed up in one word: REACH.\\n      \"],[14],[0,\"\\n\\n      \"],[11,\"p\",[]],[13],[0,\"\\n        Reaching Up | Loving God:\"],[11,\"br\",[]],[13],[14],[0,\"\\n        Vertical reach to touch God. We reach up through worship.\\n      \"],[14],[0,\"\\n      \"],[11,\"p\",[]],[13],[0,\"\\n        Reach In | Serving Each Other:\"],[11,\"br\",[]],[13],[14],[0,\"\\n        Horizontal reach to bless others.We serve God by serving each other.\\n      \"],[14],[0,\"\\n      \"],[11,\"p\",[]],[13],[0,\"\\n        Reach Out | Changing The World:\"],[11,\"br\",[]],[13],[14],[0,\"\\n        Outward reach to can change the world. We have to be bold and courageous. Jesus commanded us to go into the entire world and present the good news.\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n\\n  \"],[11,\"div\",[]],[15,\"align\",\"left\"],[13],[0,\"\\n    \"],[11,\"h2\",[]],[13],[0,\"Our Staff\"],[14],[0,\"\\n    \"],[11,\"div\",[]],[15,\"class\",\"layout-row layout-wrap\"],[13],[0,\"\\n      \"],[11,\"div\",[]],[15,\"class\",\"flex\"],[13],[0,\"\\n        \"],[11,\"div\",[]],[15,\"class\",\"aboutGallery\"],[13],[0,\"\\n          \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/Fred_Simon.jpg\"]]],[13],[0,\"\\n            \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/Fred_Simon.jpg\"]]],[15,\"alt\",\"Fred Simon\"],[15,\"width\",\"500\"],[15,\"height\",\"300\"],[13],[14],[0,\"\\n          \"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutName\"],[13],[0,\"Pastor Fred Simon\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutTitle\"],[13],[0,\"Senior Pastor\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutGalleryDesc\"],[13],[0,\"\\n            \"],[11,\"p\",[]],[13],[0,\"\\n              Before coming to Pilgrim in November 2007, Pastor Simon served a congregation in North Platte, Nebraska. He and his wife, Sue, have two daughters. Pastor Simon graduated from Concordia Seminary in St. Louis in 1984. He is also a graduate of the Pastoral Leadership Institute.\\n            \"],[14],[0,\"\\n          \"],[14],[0,\"\\n        \"],[14],[0,\"\\n        \"],[11,\"div\",[]],[15,\"class\",\"aboutGallery\"],[13],[0,\"\\n          \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/PastorNietfeld.jpg\"]]],[13],[0,\"\\n            \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/PastorNietfeld.jpg\"]]],[15,\"alt\",\"Dan Nietfeld\"],[15,\"width\",\"500\"],[15,\"height\",\"300\"],[13],[14],[0,\"\\n          \"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutName\"],[13],[0,\"Pastor Dan Nietfeld\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutTitle\"],[13],[0,\"Assistant Pastor\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutGalleryDesc\"],[13],[0,\"\\n           \"],[11,\"p\",[]],[13],[0,\"\\n            Pastor Dan and his wife Kathy are instrumental in helping to carry out the mission at Pilgrim. A former missionary to the Phillipines and a pastor for more than 30 years, Pastor Dan helps with worship, Bible studies, shut ins and more.\\n           \"],[14],[0,\"\\n          \"],[14],[0,\"\\n        \"],[14],[0,\"\\n        \"],[11,\"div\",[]],[15,\"class\",\"aboutGallery\"],[13],[0,\"\\n          \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/JenniferAnderson.jpg\"]]],[13],[0,\"\\n            \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/JenniferAnderson.jpg\"]]],[15,\"alt\",\"Jennifer Anderson\"],[15,\"width\",\"500\"],[15,\"height\",\"300\"],[13],[14],[0,\"\\n          \"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutName\"],[13],[0,\"Jennifer Anderson\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutTitle\"],[13],[0,\"Administrative Assistant\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutGalleryDesc\"],[13],[0,\"\\n            \"],[11,\"p\",[]],[13],[0,\"\\n            Jennifer is from North Platte, NE, she has been an active member of a Missouri Lutheran Church all her life.\\n            \"],[14],[0,\"\\n          \"],[14],[0,\"\\n        \"],[14],[0,\"\\n        \"],[11,\"div\",[]],[15,\"class\",\"aboutGallery\"],[13],[0,\"\\n          \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/SharonKazmierski.jpg\"]]],[13],[0,\"\\n            \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/SharonKazmierski.jpg\"]]],[15,\"alt\",\"Sharon Kazmierski\"],[15,\"width\",\"500\"],[15,\"height\",\"300\"],[13],[14],[0,\"\\n          \"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutName\"],[13],[0,\"Sharon Kazmierski\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutTitle\"],[13],[0,\"Music\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutGalleryDesc\"],[13],[0,\"\\n            \"],[11,\"p\",[]],[13],[0,\"\\n              Director at Pilgrim Lutheran Church.\\n            \"],[14],[0,\"\\n          \"],[14],[0,\"\\n        \"],[14],[0,\"\\n        \"],[11,\"div\",[]],[15,\"class\",\"aboutGallery\"],[13],[0,\"\\n          \"],[11,\"a\",[]],[15,\"target\",\"_blank\"],[16,\"href\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/JaneKemp.jpg\"]]],[13],[0,\"\\n            \"],[11,\"img\",[]],[16,\"src\",[34,[[28,[\"constants\",\"rootURL\"]],\"img/JaneKemp.jpg\"]]],[15,\"alt\",\"Jane Kemp\"],[15,\"width\",\"500\"],[15,\"height\",\"300\"],[13],[14],[0,\"\\n          \"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutName\"],[13],[0,\"Jane Kemp\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutTitle\"],[13],[0,\"Childcare Center Director at Pilgrim\"],[14],[0,\"\\n          \"],[11,\"div\",[]],[15,\"class\",\"aboutGalleryDesc\"],[13],[0,\"\\n            \"],[11,\"p\",[]],[13],[0,\"\\n\\n            \"],[14],[0,\"\\n          \"],[14],[0,\"\\n        \"],[14],[0,\"\\n      \"],[14],[0,\"\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n\\n  \"],[11,\"div\",[]],[15,\"align\",\"left\"],[13],[0,\"\\n    \"],[11,\"h2\",[]],[13],[0,\"Church Council\"],[14],[0,\"\\n    \"],[11,\"p\",[]],[13],[0,\"\\n      The Council of Ministries is the new name of the lay governing board of the church. At Pilgrim, it was previously called the Church Council. The task of the Council of Ministries is several fold. First, they are to oversee the various ministries of the church, providing guidance, support and vision for the church. The Council of Ministries also oversees the churchs finances through the Treasurer. Each member of the Council is assigned to ask as a coordinator for various ministry teams. They are to encourage and support the ministries under their care and act as a liasion for them. They are the primary planning and vision setting body of the church.\\n    \"],[14],[0,\"\\n  \"],[14],[0,\"\\n\\n  \"],[11,\"div\",[]],[15,\"align\",\"left\"],[13],[0,\"\\n    \"],[11,\"h2\",[]],[13],[0,\"The Role of the Elders\"],[14],[0,\"\\n    \"],[11,\"p\",[]],[13],[0,\"\\n      What we commonly call elders today are laymen appointed to serve the congregation in its temporal affairs and to assist the pastor in administrative tasks. An example of this is found in Acts 6:1-6:\\n    \"],[14],[0,\"\\n    \"],[11,\"p\",[]],[13],[0,\"\\n    In those days when the number of disciples was increasing, the Grecian Jews among them complained against the Hebraic Jews because their widows were being overlooked in the daily distribution of food. So the Twelve gathered all the disciples together and said, It would not be right for us to neglect the ministry of the word of God in order to wait on tables. Brothers, choose seven men from among you who are known to be full of the Spirit and wisdom. We will turn this responsibility over to them and will give our attention to prayer and the ministry of the word. This proposal pleased the whole group. They chose Stephen, a man full of faith and of the Holy Spirit; also Philip, Procorus, Nicanor, Timon, Parmenas, and Nicolas from Antioch, a convert to Judaism. They presented these men to the apostles, who prayed and laid their hands on them.\\n      \"],[14],[0,\"\\n      \"],[11,\"p\",[]],[13],[0,\"\\n    Later such men came to be known as the deacons (meaning servants). As you can see, Scripture does not define the exact role of such deacons, only their qualifications (1 Timothy 3:8-13). Scripture gives them no special spiritual responsibilities in the congregation beyond those given to every Christian.\\n      \"],[14],[0,\"\\n      \"],[11,\"p\",[]],[13],[0,\"\\n    While the office of pastor is divinely instituted and indispensable for the Church, the deacon is an optional office based on Apostolic and church custom.\\n      \"],[14],[0,\"\\n      \"],[11,\"p\",[]],[13],[0,\"\\n    The deacon or elder is a position of lay-service, concerned with the temporal and administrative affair of the congregation. In many congregations deacons or elders are also charged with oversight of the pastor. But, rightly understood according to Scripture, they exercise only that oversight given to every Christian in the congregation.\\n      \"],[14],[0,\"\\n  \"],[14],[0,\"\\n\\n\"],[14],[0,\"\\n\"]],\"locals\":[],\"named\":[],\"yields\":[],\"hasPartials\":false}", "meta": { "moduleName": "pilgrim-frontend/templates/about.hbs" } });
